.CodeMirror-hints {
    position: absolute;
    z-index: 10;
    overflow: hidden;
    list-style: none;
    margin: 0;
    padding: 3px;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    border-radius: 0;
    border: 1px solid #afb6bd;
    background: #fff;
    box-shadow: 0 0 0 1px #fff;
    font-size: 1em;
    font-family: PT Mono,Courier,monospace;
    max-height: 20em;
    overflow-y: auto
}

.CodeMirror-hint {
    margin: 0;
    padding: 0 2px;
    border-radius: 0;
    white-space: pre;
    color: #2c3e50;
    cursor: pointer;
    opacity: .5
}

.dark .CodeMirror-hints {
    background: #1d1d1d;
    border: 1px solid #626b6e;
    box-shadow: 0 0 0 1px #1d1d1d
}

.dark .CodeMirror-hint {
    color: #dde3e4
}

.dark .CodeMirror-hint-active {
