@import url(./global.css);
@import url(./codemirror.css);
@import url(./markdown.css);
@import url(./uploading.css);
@import url(./swtheme.css);

.CodeMirror {
    height: 100%;
    font-size: 16px;
}

.CodeMirror-scroll {
    padding: 10px;
}

.CodeMirror-placeholder {
    position: fixed;
    top: 5px;
    left: 5px;

    padding: 10px;
}

.CodeMirror-cursor {
    border-left: 2px solid #ff0000!important;
    width: 2px!important;
}

.CodeMirror pre {
    font-family: PT Mono, Operator Mono Lig, Fira Code, Source Code Pro, DejaVu Sans Mono, Anonymous Pro, Ubuntu Mono, Roboto Mono, JetBrains Mono, Droid Sans Mono, Mono, Monaco, Menlo, Inconsolata, Liberation Mono, ui-monospace, monospace, Microsoft YaHei UI, system-ui, -apple-system, "system-ui", sans-serif, iconfont, icomoon, FontAwesome, "Font Awesome 5 Pro", "Font Awesome 6 Pro", IcoFont, fontello, themify, Material Icons, Material Icons Extended, bootstrap-icons, Segoe Fluent Icons, Material-Design-Iconic-Font, office365icons, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji, Android Emoji, EmojiSymbols, emojione mozilla, twemoji mozilla, Symbola!important;
}

.toolbar {
    position: fixed;
    left: 10px;
    bottom: 10px;

    display: flex;
    flex-direction: row;
}

.toolbar a {
    display: flex;
    align-content: center;
    justify-content: center;

    padding: 5px;

    width: 18px;
    height: 18px;
}

#preview {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    height: 100%;

    padding: 20px 20%;

    background-color: #fff;

    animation-name: showIn;
    animation-duration: .2s;

    z-index: 2;
}

#preview .close {
    position: fixed;
    top: 40px;
    right: 21px;

    display: flex;
    justify-content: center;
    align-items: center;

    transition: all .18s ease-out;

    cursor: pointer;
}

#preview .close:hover {
    transform: rotate( 90deg );
}

#preview.hide {
    animation-name: showOut;
    animation-duration: .2s;
}

@keyframes showIn {
    0% {
        opacity: 0;
        transform: translateY(-20px)
    }

    100% {
        opacity: 1;
        transform: translateY(0)
    }
}

@keyframes showOut {
    0% {
        opacity: 1;
        transform: translateY(0)
    }

    100% {
        opacity: 0;
        transform: translateY(-20px)
    }
}