#plugin-sr-uploading {
    font-weight: 400;
    text-shadow: none!important;

    backdrop-filter: blur(4px);
    pointer-events: none;
}

#plugin-sr-uploading diglog {
    min-width: initial!important;
    max-height: 90%!important;
    max-width: 90%!important;

    pointer-events: initial;
}

#plugin-sr-uploading diglog,
#plugin-sr-uploading dialog-content {
    max-height: 90%!important;
    min-height: initial!important;
    pointer-events: initial;
}

#plugin-sr-uploading dialog-header {
    display: none!important;
}

#plugin-sr-uploading dialog-content {
    display: flex!important;
    justify-content: center;
    align-items: center;

    padding: 24px!important;
    overflow-y: overlay!important;
}

#plugin-sr-uploading dialog-content,
#plugin-sr-uploading dialog-footer {
    color: rgba(0, 0, 0, 0.870588)!important;
    background-color: rgb(255, 255, 255)!important;
}

#plugin-sr-uploading dialog-footer {
    display: none!important;
}

#plugin-sr-uploading .uploading {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 100%;

    overflow: hidden;
}

#plugin-sr-uploading .uploading svg {
    margin: 20px;
    width: 100px!important;
}

#plugin-sr-uploading .status {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    color: rgba(0, 0, 0, 0.870588)!important;

    font-size: 13px;
    text-align: center;
}

#plugin-sr-uploading .status .label {
    padding: 4px 12px;
    height: 22px;
    line-height: 1.3;
    color: rgba(0, 0, 0, 0.870588)!important;
    outline: none;
    font-weight: 500;
    background: radial-gradient(circle at 10px -7px, transparent 8px, currentColor 8px, currentColor 9px, transparent 9px) repeat-x, radial-gradient(circle at 10px 27px, transparent 8px, currentColor 8px, currentColor 9px, transparent 9px) repeat-x;
    background-size: 20px 20px;
    background-position: -10px calc(100% + 16px), 0 calc(100% - 4px);
    animation: waveFlow 1s infinite linear;background: radial-gradient(circle at 10px -7px, transparent 8px, currentColor 8px, currentColor 9px, transparent 9px) repeat-x, radial-gradient(circle at 10px 27px, transparent 8px, currentColor 8px, currentColor 9px, transparent 9px) repeat-x;
    background-size: 20px 20px;
    background-position: -10px calc(100% + 16px), 0 calc(100% - 4px);
    animation: waveFlow 1s infinite linear;

}

@keyframes waveFlow {
    from { background-position-x: -10px, 0; }
    to { background-position-x: -30px, -20px; }
}

@keyframes scaleAnimation {
    0% {
        opacity: 0;
        transform: scale(1.5);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}
@keyframes drawCircle {
    0% {
        stroke-dashoffset: 151px;
    }
    100% {
        stroke-dashoffset: 0;
    }
}
@keyframes drawCheck {
    0% {
        stroke-dashoffset: 36px;
    }
    100% {
        stroke-dashoffset: 0;
    }
}

@keyframes fadeOut {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

#successAnimationCircle {
    stroke-dasharray: 151px 151px;
    stroke: #1ECD97;
}

#successAnimationCheck {
    stroke-dasharray: 36px 36px;
    stroke: #1ECD97;
}

#successAnimationResult {
    fill: #1ECD97;
    opacity: 0;
}

#successAnimation.animated {
    animation: 1s ease-out 0s 1 both scaleAnimation;
}

#successAnimation.animated #successAnimationCircle {
    animation: 1s cubic-bezier(0.77, 0, 0.175, 1) 0s 1 both drawCircle, 0.3s linear 0.9s 1 both fadeOut;
}

#successAnimation.animated #successAnimationCheck {
    animation: 1s cubic-bezier(0.77, 0, 0.175, 1) 0s 1 both drawCheck, 0.3s linear 0.9s 1 both fadeOut;
}

#successAnimation.animated #successAnimationResult {
    animation: 0.3s linear 0.9s both fadeIn;
}

.loader,
.loader:after {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.loader {
    margin: 20px;
    font-size: 10px;
    position: relative;
    text-indent: -9999em;
    border-top: 3px solid rgba(183,31,31, 0.2);
    border-right: 3px solid rgba(183,31,31, 0.2);
    border-bottom: 3px solid rgba(183,31,31, 0.2);
    border-left: 3px solid #1ECD97;
    transform: translateZ(0);
    animation: load8 1.1s infinite linear;
}

@keyframes load8 {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

@media (prefers-color-scheme: dark) {
    #plugin-sr-uploading diglog,
    #plugin-sr-uploading dialog-content,
    #plugin-sr-uploading dialog-footer {
        color: #dbdbfd!important;
        background-color: #222!important;
    }

    #plugin-sr-uploading .status,
    #plugin-sr-uploading .status .label {
        color: #dbdbfd!important;
    }
}

/**
 * from https://github.com/Kamonlojn/svg-icons-animate
 */
#plugin-sr-uploading .uploading .svg-box {
    display:inline-block;
    position: relative;
    width:150px!important;
    transform: scale(.5);
}

#plugin-sr-uploading .uploading .svg-box svg {
    margin: 0!important;
    width: initial!important;
}

#plugin-sr-uploading .uploading .green-stroke {
    stroke:#7CB342;
}

#plugin-sr-uploading .uploading .red-stroke {
    stroke: #FF6245;
}

#plugin-sr-uploading .uploading .yellow-stroke {
    stroke: #FFC107;
}

#plugin-sr-uploading .uploading .circular circle.path {
    stroke-dasharray: 330;
    stroke-dashoffset: 0;
    stroke-linecap: round;
    opacity: 0.4;
    animation: 0.7s draw-circle ease-out;
}

#plugin-sr-uploading .uploading .checkmark{
	stroke-width: 6.25;
    stroke-linecap: round;
	position:absolute;
    top: 56px;
    left: 49px;
    width: 52px;
    height: 40px;
}

#plugin-sr-uploading .uploading .checkmark path {
    animation: 1s draw-check ease-out;
}

@keyframes draw-circle {
    0% {
        stroke-dasharray: 0,330;
        stroke-dashoffset: 0;
        opacity: 1;
    }

    80% {
        stroke-dasharray: 330,330;
        stroke-dashoffset: 0;
        opacity: 1;
    }

    100%{
        opacity: 0.4;
    }
}

@keyframes draw-check {
    0% {
        stroke-dasharray: 49,80;
        stroke-dashoffset: 48;
        opacity: 0;
    }

    50% {
        stroke-dasharray: 49,80;
        stroke-dashoffset: 48;
        opacity: 1;
    }

    100% {
        stroke-dasharray: 130,80;
        stroke-dashoffset: 48;
    }
}

#plugin-sr-uploading .uploading .cross {
    stroke-width:6.25;
    stroke-linecap: round;
    position: absolute;
    top: 54px;
    left: 54px;
    width: 40px;
    height: 40px;
}

#plugin-sr-uploading .uploading .cross .first-line {
    animation: 0.7s draw-first-line ease-out;
}

#plugin-sr-uploading .uploading .cross .second-line {
    animation: 0.7s draw-second-line ease-out;
}

@keyframes draw-first-line {
    0% {
        stroke-dasharray: 0,56;
        stroke-dashoffset: 0;
    }

    50% {
        stroke-dasharray: 0,56;
        stroke-dashoffset: 0;
    }

    100% {
        stroke-dasharray: 56,330;
        stroke-dashoffset: 0;
    }
}

@keyframes draw-second-line {
    0% {
        stroke-dasharray: 0,55;
        stroke-dashoffset: 1;
    }

    50% {
        stroke-dasharray: 0,55;
        stroke-dashoffset: 1;
    }

    100% {
        stroke-dasharray: 55,0;
        stroke-dashoffset: 70;
    }
}

#plugin-sr-uploading .uploading .alert-sign {
    stroke-width:6.25;
    stroke-linecap: round;
    position: absolute;
    top: 40px;
    left: 68px;
    width: 15px;
    height: 70px;
    animation: 0.5s alert-sign-bounce cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

#plugin-sr-uploading .uploading .alert-sign .dot {
    stroke:none;
    fill: #FFC107;
}

@keyframes alert-sign-bounce {
    0% {
        transform: scale(0);
        opacity: 0;
    }

    50% {
        transform: scale(0);
        opacity: 1;
    }

    100% {
        transform: scale(1);
    }
}